<template>
  <article v-if="this.productsData">
    <button type="button" class="btn btn-primary mb-3" @click="openModal">新增產品</button>
    <Modal/>
    <section>
      <table class="table table-hover">
        <thead>
          <tr>
            <th scope="col">編號</th>
            <th scope="col">圖片</th>
            <th scope="col">標題</th>
            <th scope="col">性別</th>
            <th scope="col">種類</th>
            <th scope="col">價格</th>
            <th scope="col">修改</th>
            <th scope="col">刪除</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(product,index) in productsData" :key="index">
            <th scope="row">{{product.product_id}}</th>
            <td>
              <img :src="imgUrl(product.product_id)" class="product_img">
            </td>
            <td>{{product.title}}</td>
            <td>{{product.gender}}</td>
            <td>{{product.category}}</td>
            <td>{{product.price}}</td>
            <td>
              <button @click.prevent="modifyProduct(product.product_id)">修改</button>
            </td>
            <td>
              <button @click.prevent="deleteProduct(product.product_id)">刪除</button>
            </td>
          </tr>
        </tbody>
      </table>

    </section>
  </article>
</template>
<script>
import AxiosClass from "@/config/axiosClass.js";
import $ from "jquery";
import Modal from './admin_product_edit.vue'
export default {
  components: {
    Modal
  },
  data() {
    return {
      productsData: []
    };
  },
  methods: {
    openModal() {
      $("#exampleModalCenter").modal("show");
    },
    cleanTempData() {
      this.tempData = this.emptyData;
      this.colorNum = 0;
    },
    imgUrl(product_id) {
      return `${process.env.VUE_APP_BACKURL}img/${product_id}/1.jpg`;
    },
    getProducts() {
      const self = this;
      const getProducts = new AxiosClass("admin/product", "get", null, true);
      getProducts.doAxios(
        self,
        function(res) {
          self.productsData = res.data.data;
          self.$emit("push-alert", "抓取商品成功");
        },
        function(res) {
          self.$emit("push-alert", "抓取商品失敗", true);
        }
      );
    },
    addProduct() {
      const self = this;
      const addProduct = new AxiosClass(
        "admin/product",
        "post",
        this.addProductData,
        true
      );
      addProduct.doAxios(
        self,
        function(res) {
          self.productsData = res.data.data;
          self.$emit("push-alert", "新增商品失敗")
        },
        function(res) {
          self.$emit("push-alert", "抓取商品失敗", true);}
      );
    },
    deleteProduct(product_id) {
      const self = this;
      const addProduct = new AxiosClass(
        `admin/product${product_id}`,
        "delete",
        null,
        true
      );
      addProduct.doAxios(
        self,
        function(res) {
          self.productsData = res.data.data;
        },
        function(res) {}
      );
    },
    modifyProduct(product_id) {
      const self = this;
      const addProduct = new AxiosClass(
        `admin/product${product_id}`,
        "put",
        this.addProductData,
        true
      );
      addProduct.doAxios(
        self,
        function(res) {
          self.productsData = res.data.data;
        },
        function(res) {
          console.log(res);
        }
      );
    },
  },
  created() {
    this.getProducts();
  }
};
</script>

<style lang="scss" scoped>
.product_img {
  height: 50px;
}
</style>
