<template>
  <article
    class="modal fade"
    id="exampleModalCenter"
    tabindex="-1"
    role="dialog"
    aria-labelledby="exampleModalCenterTitle"
    aria-hidden="true"
  >
    <div class="modal-dialog modal-lg modal-dialog-centered" role="document">
      <div class="modal-content">
        <div class="modal-header">
          <h5 class="modal-title" id="exampleModalCenterTitle">新增產品</h5>
          <button
            type="button"
            @click="cleanTempData()"
            class="close"
            data-dismiss="modal"
            aria-label="Close"
          >
            <span aria-hidden="true">&times;</span>
          </button>
        </div>
        <section class="modal-body">
          <form>
            <div class="form-row mb-5">
              <div class="col">
                名稱
                <input type="text" class="form-control" placeholder="名稱" v-model="tempData.title">
              </div>
              <div class="col">
                性別
                <select class="custom-select mr-sm-2" v-model="tempData.gender">
                  <option selected>請選擇...</option>
                  <option value="women">女性</option>
                </select>
              </div>
              <div class="col" v-if="isTempCategoryShow">
                種類
                <select class="custom-select mr-sm-2" v-model="tempData.category">
                  <option selected>請選擇...</option>
                  <option :value="item.id" v-for="(item,index) in tempCategory" :key="index">{{item.name}}</option>
                </select>
              </div>
              <div class="col">
                價格
                <input type="number" class="form-control" placeholder="價格">
              </div>
            </div>
            <button class="btn btn-primary" @click.prevent="addEmptyColor">新增樣式</button>
            <section
              class="form-row my-5 border p-2"
              v-for="(color,index) in tempData.colors" :key="index">
              <div class="col">
                <input type="file" class="filelisten btn btn-primary" v-filelisten="color.colorId">
                <img class="imglisten">
              </div>
              <div class="col">
                <div class="form-row">
                  <div class="col">樣式編號{{index+1}}</div>
                  <div class="col">
                    <button class="btn btn-primary" @click.prevent="deleteColor(index)">刪除</button>
                  </div>
                </div>
                <div class="form-row">
                  <div class="col">
                    樣式色碼
                    <input
                      type="text"
                      class="form-control"
                      placeholder="色碼"
                      v-model="tempData['colors'][index]['colorNum']"
                    >
                  </div>
                  <div class="col">
                    樣式名稱
                    <input
                      type="text"
                      class="form-control"
                      placeholder="顏色名稱"
                      v-model="tempData.colors[index].colorName"
                    >
                  </div>
                </div>
                <div class="form-row">
                  <div class="form-check form-check-inline">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="inlineCheckbox1"
                      value="option1"
                    >
                    <label class="form-check-label" for="inlineCheckbox1">S</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="inlineCheckbox2"
                      value="option2"
                    >
                    <label class="form-check-label" for="inlineCheckbox2">M</label>
                  </div>
                  <div class="form-check form-check-inline">
                    <input
                      class="form-check-input"
                      type="checkbox"
                      id="inlineCheckbox3"
                      value="option3"
                      disabled
                    >
                    <label class="form-check-label" for="inlineCheckbox3">3 (disabled)</label>
                  </div>
                </div>
              </div>
            </section>
            <section></section>
          </form>
        </section>
        <div class="modal-footer">
          <button
            type="button"
            class="btn btn-secondary"
            data-dismiss="modal"
            @click="cleanTempData()"
          >關閉</button>
          <button type="button" class="btn btn-primary">儲存</button>
        </div>
      </div>
    </div>
  </article>
</template>
<script>
import AxiosClass from "@/config/axiosClass.js";
export default {
  data() {
    return {
      isTempCategoryShow: false,
      colorCounter: 0,
      tempCategory: [],
      tempData: {
        title: "",
        gender: "",
        category: "",
        price: 0,
        colorImgNum: 1,
        introductionImgNum: 0,
        globalImg: [],
        colors: []
      },
      emptyColor: {
        colorId: "",
        colorNum: "",
        colorName: "",
        sizes: [
          {
            sizeId: "",
            sizeName: "",
            sizeStock: ""
          }
        ]
      },
      emptyData: {
        title: "",
        gender: "",
        category: "",
        price: 0,
        colorImgNum: 1,
        introductionImgNum: 0,
        globalImg: [],
        colors: []
      }
    };
  },
  computed: {
    tempGender() {
      return this.tempData.gender;
    }
  },
  watch: {
    tempGender(val, oldVal) {
      if (val) {
        this.getCategory();
      }
    }
  },
  methods: {
    deleteColor(colorIndex) {
      this.tempData.colors.splice(colorIndex, 1);
    },
    cleanTempData() {
      this.tempData = this.emptyData;
    },
    addEmptyColor() {
      const empty = Object.assign({},this.emptyColor)
      this.tempData.colors.push(empty)
    },
    getCategory() {
      const self = this;
      const getCategory = new AxiosClass(
        `admin/category_gender/${self.tempGender}`,
        "get",
        null,
        true
      );
      getCategory.doAxios(
        self,
        function(res) {
          for (const bigCategory of res.data.data) {
            self.tempCategory.push(...bigCategory.category);
            self.isTempCategoryShow = true;
          self.$emit("push-alert", "讀取種類成功");
          }
        },
        function(res) {
          self.$emit("push-alert", "讀取種類失敗", true);
        }
      );
    }
  },
  directives: {
    filelisten: {
      bind: function(el, binding) {
        if (binding.value) {
          el.nextSibling.src = binding.value;
        }
        el.addEventListener("change", function() {
          let reader = new FileReader();
          reader.addEventListener("load", function() {
            el.nextSibling.src = reader.result;
          });
          reader.readAsDataURL(el.files[0]);
        });
      }
    }
  }
};
</script>
